import * as React from "react";
import SEO from "Components/SEO";
import { PAGE_CONTENT_TYPE } from "Constants/contentful";
import { PromiseState } from "Constants/promise";
import { Entry } from "contentful";
import ContentfulClientContext from "Contexts/contentful";
import { IPage } from "Types/contentful";
import render from "Utils/renderContentfulComponent";

type IProps = {
	path: string;
};

const Page: React.FC<IProps> = ({ path }: IProps) => {
	const client = React.useContext(ContentfulClientContext);

	const [page, setPage] = React.useState<Entry<IPage>>();
	const [loadingState, setLoadingState] = React.useState<PromiseState | void>();

	React.useEffect(() => {
		if (!client) {
			return;
		}

		const getEntriesFn = async (path: string): Promise<void> => {
			setLoadingState(PromiseState.PENDING);
			try {
				const response = await client.getEntries<IPage>({
					content_type: PAGE_CONTENT_TYPE,
					"fields.path": path,
					// We want to resolve references in navigationBar
					include: 4,
				});
				if (response.items.length === 0) {
					throw new Error();
				}
				setPage(response.items[0]);
				setLoadingState(PromiseState.FULFILLED);
				return;
			} catch (e) {
				// We will need a <Redirect> when we enable a router.
				return getEntriesFn("404");
			}
		};

		getEntriesFn(path)
			.then(() => {
				return;
			})
			.catch(() => {
				setLoadingState(PromiseState.REJECTED);
			});
	}, [client, path]);

	if (
		loadingState === PromiseState.REJECTED ||
		(loadingState === PromiseState.FULFILLED && !page)
	) {
		return <p>There was an error</p>;
	}

	if (loadingState === PromiseState.PENDING || !page) {
		return <p>Loading</p>;
	}

	return (
		<div id="start">
			<SEO
				title={page.fields.title}
				description={page.fields.description}
				keywords={page.fields.keywords}
			/>

			{page.fields.components.map(render)}
		</div>
	);
};

export default Page;
