import * as React from "react";
import { ILinkGroup } from "Types/contentful";
import styles from "SCSS/footer.module.scss";
import A from "Components/Shared/Anchor";

const LinkGroup: React.FC<ILinkGroup> = ({
	linkGroupTitle,
	showTitle,
	links,
}: ILinkGroup) => {
	return (
		<section className={styles["link-group"]}>
			{showTitle ? <h6 className={styles.title}>{linkGroupTitle}</h6> : null}
			<nav className={styles.nav}>
				<ul>
					{links.map((link) => {
						return (
							<li key={link.sys.id}>
								<A {...link.fields}>{link.fields.linkTitle}</A>
							</li>
						);
					})}
				</ul>
			</nav>
		</section>
	);
};

export default LinkGroup;
