import React from "react";
import Wrapper from "@smallsforsmalls/wrapper";
import Grid from "@smallsforsmalls/grid";
import Image from "@smallsforsmalls/image";
import Carousel from "Components/Carousel";
import TextBlock from "@smallsforsmalls/textblock";
import { Button } from "@smallsforsmalls/button";
import styles from "SCSS/centerAlignedText.module.scss";
import clsx from "clsx";
import { ICenterAlignedText } from "Types/contentful";

const CenterAlignedText: React.FC<ICenterAlignedText> = ({
	theme,
	backgroundColor,
	centerAlignedTextTitle,
	showTitle = true,
	button,
	items,
	icon,
	moduleBorder = false,
	useCarousel = false,
	carouselNavigation = "dots",
	carouselMode = "full",
}: ICenterAlignedText) => {
	const classNames = clsx({
		[styles["centeralignedtext-content"]]: true,
		[styles["-off"]]: !useCarousel,
	});

	const slidesClassNames = clsx(styles.slides, {
		[styles["-one"]]: items?.length === 1,
		[styles["-two"]]: items?.length === 2,
		[styles["-three"]]: items?.length === 3,
		[styles["-four"]]: items?.length === 4,
		[styles["-five"]]: items?.length === 5,
	});

	const slides = items?.map((el, index) => {
		return (
			<div className={slidesClassNames} key={`${el.fields.title}-${index}`}>
				<Image
					src={el.fields.mainImage.fields.file.url}
					description={el.fields.mainImage.fields.description}
					title={el.fields.mainImage.fields.title}
				/>
				<TextBlock headingLevel="paragraph" title={el.fields.title} />
			</div>
		);
	});

	const showCarousel = items && items.length > 1 && useCarousel;

	return (
		<Wrapper
			moduleBorder={moduleBorder}
			theme={theme}
			bgColor={backgroundColor}
		>
			<Grid mode="full">
				<div className={classNames}>
					{icon && (
						<Image
							src={icon.fields.file.url}
							title={icon.fields.title}
							description={icon.fields.description}
						/>
					)}
					{showTitle && <h3>{centerAlignedTextTitle}</h3>}
					{slides && showCarousel ? (
						<Carousel
							until="mobile"
							mode={carouselMode}
							navigation={carouselNavigation}
							cols={20}
						>
							{slides}
						</Carousel>
					) : (
						slides && <div>{slides}</div>
					)}
					{button && (
						<div className={styles.cta}>
							<Button
								btnType={button.fields.buttonType ? "primary" : "secondary"}
							>
								{button.fields.buttonLabel}
							</Button>
						</div>
					)}
				</div>
			</Grid>
		</Wrapper>
	);
};

export default CenterAlignedText;
