import * as React from "react";
import { ILink } from "Types/contentful";

type IProps = Omit<
	React.DetailedHTMLProps<
		React.AnchorHTMLAttributes<HTMLAnchorElement>,
		HTMLAnchorElement
	>,
	"href"
> &
	ILink;

const A = (props: IProps): JSX.Element => {
	const { href, externalHref, children, ...rest } = props;

	if (!(href || externalHref)) {
		return <></>;
	}

	const hrefAttr =
		typeof externalHref === "string" ? externalHref : href?.fields.path;
	delete rest.isChild;
	delete rest.linkTitle;
	delete rest.mediaResource;

	return (
		<a {...rest} href={hrefAttr} target={externalHref ? "_blank" : undefined}>
			{children}
		</a>
	);
};

export default A;
