import * as React from "react";
import { IUpItem } from "Types/contentful";
import styles from "SCSS/upItem.module.scss";
import Image from "@smallsforsmalls/image";
import A from "Components/Shared/Anchor";
import clsx from "clsx";

type IProps = IUpItem & {
	className?: string;
	layout: ["Image"] | ["Icon"] | ["Product"];
	useCarousel?: boolean;
};

const UpItem: React.FC<IProps> = (props: IProps) => {
	const {
		upItemTitle,
		description,
		cta,
		label,
		desktopImage,
		mobileImage,
		className,
		layout,
		useCarousel,
	} = props;

	const desktopImageFields = desktopImage.fields;

	const responsiveImages = [
		{
			id: desktopImage.sys.id,
			width: 10000,
			url: desktopImage.fields.file.url,
		},
	];

	if (mobileImage) {
		responsiveImages.unshift({
			id: mobileImage.sys.id,
			width: 900,
			url: mobileImage.fields.file.url,
		});
	}

	const itemClassName = clsx(className, styles.item, {
		[styles["-layout-product"]]: layout[0] === "Product",
		[styles["-layout-icon"]]: layout[0] === "Icon",
		[styles["-layout-image"]]: layout[0] === "Image",
		[styles["-with-carousel"]]: useCarousel,
	});

	return (
		<div className={itemClassName}>
			<h6 className={styles.label}>{label}</h6>
			<div className={styles.picture}>
				<Image
					images={responsiveImages}
					description={desktopImageFields.description}
					src={desktopImageFields.file.url}
					title={desktopImageFields.title}
				/>
			</div>
			<h4 className={styles.title}>{upItemTitle}</h4>
			<div className={styles.description}>
				<p>{description}</p>
				{cta ? <A {...cta.fields}>{cta.fields.linkTitle}</A> : null}
			</div>
		</div>
	);
};

export default UpItem;
