import * as React from "react";
import { Entry } from "contentful";
import { ILink } from "Types/contentful";
import A from "Components/Shared/Anchor";
import styles from "SCSS/footer.module.scss";

type IProps = {
	links: Entry<ILink>[];
};

const SocialNav: React.FC<IProps> = ({ links }: IProps) => {
	return (
		<ul className={styles["social-nav"]}>
			{links.map((link) => (
				<li className={styles.item} key={link.sys.id}>
					<A {...link.fields}>
						{link.fields.mediaResource ? (
							<img
								src={link.fields.mediaResource.fields.file.url}
								alt={link.fields.linkTitle}
								className={styles.image}
							/>
						) : (
							link.fields.linkTitle
						)}
					</A>
				</li>
			))}
		</ul>
	);
};

export default SocialNav;
