import * as React from "react";
import { Helmet } from "react-helmet-async";

type Props = {
	title: string;
	description?: string;
	keywords?: string;
};

const SEO: React.FC<Props> = (props: Props) => {
	const meta = [];
	if (props.description) {
		meta.push({ name: "description", content: props.description });
	}
	if (props.keywords) {
		meta.push({ name: "keywords", content: props.keywords });
	}

	return <Helmet meta={meta} title={props.title} />;
};

export default SEO;
