import React from "react";
import { useEmblaCarousel } from "embla-carousel/react";
import clsx from "clsx";
import styles from "SCSS/carousel.module.scss";
import { Indicators } from "@smallsforsmalls/indicators";
import { ReactComponent as ChevronNext } from "Images/chevron-next.svg";
import { ReactComponent as ChevronPrev } from "Images/chevron-prev.svg";
import debounce from "lodash.debounce";

type CarouselProps = {
	navigation?: "dots" | "arrows" | "none";
	mode?: "peaking" | "full";
	children: React.ReactNodeArray;
	until?: "mobile" | "tablet";
	cols?: 20 | 21 | 28;
};

const Carousel: React.FC<CarouselProps> = ({
	navigation,
	mode = "full",
	children,
	until,
	cols,
}: CarouselProps) => {
	const [emblaRef, embla] = useEmblaCarousel({ loop: false });
	const [scrollSnaps, setScrollSnaps] = React.useState<number[]>([]);
	const [selectedIndex, setSelectedIndex] = React.useState<number>(0);
	const [init, setInit] = React.useState<boolean | null>(true);
	const [windowSize, setWindowSize] = React.useState<number>(0);
	const [prevBtnEnabled, setPrevBtnEnabled] = React.useState<boolean>(false);
	const [nextBtnEnabled, setNextBtnEnabled] = React.useState<boolean>(false);

	const onSelect = React.useCallback(() => {
		if (!embla) return;
		setSelectedIndex(embla.selectedScrollSnap());
		setPrevBtnEnabled(embla.canScrollPrev());
		setNextBtnEnabled(embla.canScrollNext());
	}, [embla, setSelectedIndex]);

	// Handles indicators (dots)
	const scrollTo = React.useCallback(
		(ev, index: number) => embla && embla.scrollTo(index),
		[embla],
	);

	// Handles next and prev arrows
	const scrollPrev = React.useCallback(() => embla && embla.scrollPrev(), [
		embla,
	]);
	const scrollNext = React.useCallback(() => embla && embla.scrollNext(), [
		embla,
	]);

	// Will calculate the viewport width
	React.useEffect(() => {
		const handleResize = debounce(() => {
			setWindowSize(window.innerWidth);
		}, 100);
		window.addEventListener("resize", handleResize);
		handleResize();
		return () => window.removeEventListener("resize", handleResize);
	}, []);

	React.useEffect(() => {
		const breakpoint = until === "mobile" ? 720 : 900;
		// If the window size is 900 || 720 or less,
		// it will set init to true and will initialize the carousel
		// If not, it will set init to false and destroy the carousel
		setInit(windowSize <= breakpoint);
	}, [until, windowSize]);

	React.useEffect(() => {
		if (!embla) return;
		setScrollSnaps(embla?.scrollSnapList());
		onSelect();
		embla.on("select", onSelect);
	}, [embla, setScrollSnaps, onSelect]);

	const carouselClassNames = clsx({
		[styles.carousel]: init === true,
		[styles["-indicators"]]: navigation === "dots",
	});

	const slidesClassNames = clsx({
		[styles.slides]: true,
		[styles["-on"]]: init === true,
		[styles["-full"]]: mode === "full",
		[styles["-peaking"]]: mode === "peaking",
		[styles["-col20"]]: cols === 20,
		[styles["-col21"]]: cols === 21,
		[styles["-col28"]]: cols === 28,
	});

	const arrowClassNames = styles.arrows;

	return (
		<React.Fragment>
			<div className={carouselClassNames}>
				<div className={slidesClassNames} ref={init ? emblaRef : null}>
					<div>{children}</div>
				</div>
				{init && navigation === "arrows" && (
					<React.Fragment>
						<button
							className={arrowClassNames}
							onClick={scrollPrev}
							disabled={!prevBtnEnabled}
						>
							<ChevronPrev role="img" aria-label="Prev Slide" />
						</button>
						<button
							className={arrowClassNames}
							onClick={scrollNext}
							disabled={!nextBtnEnabled}
						>
							<ChevronNext role="img" aria-label="Next Slide" />
						</button>
					</React.Fragment>
				)}
				{init && navigation === "dots" && (
					<Indicators
						total={scrollSnaps.length}
						index={selectedIndex}
						onClick={scrollTo}
					/>
				)}
			</div>
		</React.Fragment>
	);
};

export default Carousel;
