import * as React from "react";
import Button from "@smallsforsmalls/button";
import clsx from "clsx";
import debounce from "lodash.debounce";
import styles from "SCSS/nav.module.scss";
import { INav, ILink } from "Types/contentful";
import { ReactComponent as Close } from "Images/close.svg";
import { ReactComponent as Logo } from "Images/logo.svg";
import { ReactComponent as Menu } from "Images/menu.svg";
import A from "Components/Shared/Anchor";

const LiLink: React.FC<ILink> = (
	props: ILink & { children?: React.ReactNode },
) => {
	const { children } = props;
	const liClassName = clsx(styles.item, {
		[styles["-child"]]: !!props.isChild,
	});
	return (
		<li className={liClassName}>
			<A className={styles.link} {...props}>
				{children}
			</A>
		</li>
	);
};

const Nav: React.FC<INav> = ({
	leftAlignedLinks,
	rightAlignedLinks,
	theme,
	backgroundColor: backgroundColorProp = "#f0ece5",
	logoHref = "/",
	getStartedPath = "/",
	getStartedCta = "Get Started",
}: INav): JSX.Element => {
	const [isLeftNavVisible, setLeftNavVisibility] = React.useState(false);
	const [backgroundColor, setBackgroundColor] = React.useState(
		window.scrollY === 0 ? "transparent" : backgroundColorProp,
	);

	const toggleLeftNavVisibility = React.useCallback(() => {
		setLeftNavVisibility(!isLeftNavVisible);
	}, [setLeftNavVisibility, isLeftNavVisible]);

	React.useEffect(() => {
		const onScroll = debounce(() => {
			setBackgroundColor(
				window.scrollY === 0 ? "transparent" : backgroundColorProp,
			);
		}, 100);
		window.addEventListener("scroll", onScroll);
		return () => window.removeEventListener("scroll", onScroll);
	}, [backgroundColorProp]);

	const rightNavClassName = `${styles.nav} ${styles["-right"]}`;
	const fillColorClassName = clsx({
		[styles["-dark"]]: theme,
		[styles["-light"]]: !theme,
	});

	const rightNav = rightAlignedLinks ? (
		<nav className={rightNavClassName}>
			<ul className={styles.list}>
				{rightAlignedLinks.map((link) => (
					<LiLink key={link.sys.id} {...link.fields}>
						{link.fields.linkTitle}
					</LiLink>
				))}
			</ul>
		</nav>
	) : (
		<div className={rightNavClassName} />
	);

	const headerClassName = clsx(fillColorClassName, styles.header);

	const mobileNavClassName = clsx(styles["mobile-nav"], fillColorClassName);

	const navToggleClassName = clsx(styles["nav-toggler"], fillColorClassName);

	const hamburguerClassName = clsx(navToggleClassName, styles.hamburguer);

	return (
		<React.Fragment>
			<header
				style={{ backgroundColor: backgroundColor }}
				className={headerClassName}
			>
				<h1 className={styles.heading}>
					<a href={logoHref}>
						<Logo className={styles.logo} role="img" aria-label="Smalls" />
					</a>
				</h1>
				{leftAlignedLinks ? (
					<button
						className={hamburguerClassName}
						onClick={toggleLeftNavVisibility}
					>
						<Menu
							role="img"
							className={styles.image}
							aria-label="Open navigation menu"
						/>
					</button>
				) : null}
				{rightNav}
			</header>
			{leftAlignedLinks && isLeftNavVisible ? (
				<React.Fragment>
					<button
						className={styles.shade}
						onClick={toggleLeftNavVisibility}
						aria-label="Close navigation"
					/>
					<nav className={mobileNavClassName}>
						<button
							className={navToggleClassName}
							onClick={toggleLeftNavVisibility}
						>
							<Close
								role="img"
								className={styles.image}
								aria-label="Close navigation menu"
							/>
						</button>
						<ul className={styles.list}>
							{leftAlignedLinks.map((link) => (
								<LiLink {...link.fields} key={link.sys.id}>
									{link.fields.linkTitle}
								</LiLink>
							))}
						</ul>
						<a href={getStartedPath}>
							<Button className={styles["get-started-cta"]} btnType="primary">
								{getStartedCta}
							</Button>
						</a>
					</nav>
				</React.Fragment>
			) : null}
		</React.Fragment>
	);
};

export default Nav;
