import * as React from "react";
import ATF from "@smallsforsmalls/atf";
import Footer from "Components/Footer";
import Nav from "Components/Nav";
import CenterAlignedText from "Components/CenterAlignedText";
import {
	ATF_CONTENT_TYPE,
	FOOTER_CONTENT_TYPE,
	NAV_CONTENT_TYPE,
	CENTERALIGNEDTEXT_CONTENT_TYPE,
	THREE_UP_CONTENT_TYPE,
	THREE_UP_ICONS_CONTENT_TYPE,
} from "Constants/contentful";
import { PageComponent } from "Types/contentful";
import ThreeUp from "Components/ThreeUp";

const Components = {
	[ATF_CONTENT_TYPE](props: PageComponent) {
		if (!("atfTitle" in props)) {
			throw new Error();
		}
		return <ATF {...props} />;
	},
	[CENTERALIGNEDTEXT_CONTENT_TYPE](props: PageComponent) {
		if (!("centerAlignedTextTitle" in props)) {
			throw new Error();
		}
		return <CenterAlignedText {...props} />;
	},
	[FOOTER_CONTENT_TYPE](props: PageComponent) {
		if (!("footerTitle" in props)) {
			throw new Error();
		}
		return <Footer {...props} />;
	},
	[NAV_CONTENT_TYPE](props: PageComponent) {
		if (!("navTitle" in props)) {
			throw new Error();
		}
		return <Nav {...props} />;
	},
	[THREE_UP_CONTENT_TYPE](props: PageComponent) {
		if (!("threeUpTitle" in props)) {
			throw new Error();
		}
		return <ThreeUp {...props} />;
	},
	[THREE_UP_ICONS_CONTENT_TYPE](props: PageComponent) {
		if (!("threeUpTitle" in props)) {
			throw new Error();
		}
		return <ThreeUp {...props} />;
	},
};

export default Components;
