import * as React from "react";
import { IThreeUp, IThreeUpIcon } from "Types/contentful";
import A from "Components/Shared/Anchor";
import Grid from "@smallsforsmalls/grid";
import styles from "SCSS/threeUp.module.scss";
import Button from "@smallsforsmalls/button";
import UpItem from "Components/Shared/UpItem";
import Carousel from "Components/Carousel";
import clsx from "clsx";

const Layouts = {
	Product: styles["-layout-product"],
	Image: styles["-layout-image"],
	Icon: styles["-layout-icon"],
} as const;

const ThreeUp: React.FC<IThreeUp | IThreeUpIcon> = (
	props: IThreeUp | IThreeUpIcon,
) => {
	const { threeUpTitle, backgroundColor, items } = props;

	// The "3UP Icon" does not provide a layout.
	// We have to fool TS here.
	const Icon: ["Icon"] = ["Icon"];

	const description = "description" in props && props.description;
	const cta = "cta" in props && props.cta;
	const ctaTheme = "ctaTheme" in props && props.ctaTheme;
	const useCarousel = "useCarousel" in props && props.useCarousel;
	const layout =
		"layout" in props && props.layout != null ? props.layout : Icon;

	const itemsNode = items.map((item) => (
		<UpItem
			{...item.fields}
			layout={layout}
			className={styles.item}
			key={`${item.sys.id}`}
			useCarousel={useCarousel}
		/>
	));

	const descriptionElement = description ? (
		<p className={styles.description}>{description}</p>
	) : null;

	// eslint-disable-next-line
	const wrapperClassName = clsx(styles["three-up"], Layouts[layout[0]], {
		[styles["-with-carousel"]]: useCarousel,
		[styles["-greige"]]: backgroundColor,
	});

	return (
		<div className={wrapperClassName}>
			<Grid>
				<div>
					<header className={styles.header}>
						<h2 className={styles["main-title"]}>{threeUpTitle}</h2>
						{descriptionElement}
					</header>
					{useCarousel ? (
						<div className={styles.items}>
							<Carousel cols={28} navigation="dots" until="tablet">
								{itemsNode}
							</Carousel>
						</div>
					) : (
						<div className={styles.items}>{itemsNode}</div>
					)}
					{cta ? (
						<p className={styles.cta}>
							<A {...cta.fields}>
								<Button btnType="primary" theme={ctaTheme ? "dark" : "light"}>
									{cta.fields.linkTitle}
								</Button>
							</A>
						</p>
					) : null}
				</div>
			</Grid>
		</div>
	);
};

export default ThreeUp;
