import * as React from "react";
import Components from "Components";
import { Entry } from "contentful";
import { PageComponent } from "Types/contentful";
import { PropertyOf } from "Types/PropertyOf";

export const getReactComponent = (
	contentTypeId: string,
): PropertyOf<typeof Components> | void => {
	const entry = Object.entries(Components).find(
		(component) => component[0] === contentTypeId,
	);

	return entry && entry[1];
};

export const renderComponent = (
	component: Entry<PageComponent>,
): JSX.Element | void => {
	const Component = getReactComponent(component.sys?.contentType?.sys?.id);
	return (
		Component && <Component {...component.fields} key={component.sys.id} />
	);
};

export default renderComponent;
