import * as React from "react";
import LinkGroup from "Components/Shared/LinkGroup";
import { IFooter } from "Types/contentful";
import styles from "SCSS/footer.module.scss";
import Grid from "@smallsforsmalls/grid";
import SocialNav from "Components/Shared/SocialNav";
import A from "Components/Shared/Anchor";
import ArrowUp from "Images/arrow-up.svg";

const Footer: React.FC<IFooter> = ({
	links,
	contactTitle,
	contactText,
	newsletterTitle,
	newsletterText,
	legalLinks,
	socialLinks,
	copyrightText,
	copyrightImage,
}: IFooter) => {
	return (
		<footer className={styles.footer}>
			<Grid>
				<div>
					{links.map((linkGroup) => (
						<LinkGroup {...linkGroup.fields} key={linkGroup.sys.id} />
					))}

					<section className={styles.contact}>
						<h6 className={styles.title}>{contactTitle}</h6>
						<p>{contactText}</p>
					</section>

					{(newsletterText || newsletterTitle) && (
						<section className={styles.newsletter}>
							{newsletterTitle && (
								<h6 className={styles.title}>{newsletterTitle}</h6>
							)}
							{newsletterText && <p>{newsletterText}</p>}
						</section>
					)}
					{socialLinks && <SocialNav links={socialLinks.fields.links} />}
					<nav className={styles.legal}>
						<ul>
							{legalLinks.fields.links.map((link) => {
								return (
									<li key={link.sys.id} className={styles.item}>
										<A {...link.fields}>{link.fields.linkTitle}</A>
									</li>
								);
							})}
						</ul>
					</nav>
					<div className={styles.copyright}>
						<img src={copyrightImage.fields.file.url} alt={copyrightText} />
					</div>
					<a href="#start" className={styles["back-to-top"]}>
						<img src={ArrowUp} alt="Back to top" />
					</a>
				</div>
			</Grid>
		</footer>
	);
};

export default Footer;
