import * as React from "react";
import ContentfulClientContext from "Contexts/contentful";
import Page from "Components/Page";
import { ContentfulClient } from "Types/contentful";
import {
	createClient as contentfulCreateClient,
	CreateClientParams,
} from "contentful";

type IProps = {
	createClient: typeof contentfulCreateClient;
	contentfulArgs: CreateClientParams;
};

function App({ createClient, contentfulArgs }: IProps): JSX.Element {
	const [client, setClient] = React.useState<ContentfulClient | void>();
	React.useEffect(() => {
		try {
			const client = createClient(contentfulArgs);
			setClient(client);
		} catch {
			// eslint-disable-next-line no-unused-expressions
			(e: Error) => e;
		}
	}, [createClient, contentfulArgs]);

	const path = window.location.pathname;

	return (
		<ContentfulClientContext.Provider value={client}>
			<Page path={path === "/" ? path : path.replace("/", "")} />
		</ContentfulClientContext.Provider>
	);
}

export default App;
